<template>
<div class="goods">
     <div class="header">
        <div class="span11" @click="$router.go(-1)">
          <div><i class="el-icon-arrow-left" ></i> </div>
          
        </div>
        <div class="span2">
          <span>{{bgood}}</span>
        </div>
          <div class="span3">
            <div v-if="show==false" @click="show=true">编辑</div>
            <div v-else @click="show=false">完成</div>
            
        </div>
    </div>
       <div class="content">
           <div class="over" v-for="(item,key) in list" :key="key">
               <ul>
                   <li>{{item.address_detail}}</li>
                   <li>{{item.phone}}</li>
               </ul>
               <div v-show="show" @click="btn(item.id)">x</div>
           </div>
           <div class="zeng" @click="logoods"><span>新增地址</span><span class="el-icon-arrow-right"></span></div>
       </div>
     
     
</div>
</template>
<script>
import axios from 'axios'
import Head from '@/components/head/Head'
export default {
    name:'goods',
    components:{Head},
    data(){
        return{
            bgood:'编辑地址',
            list:[],
            show:false
        }
    },
    mounted(){
        //渲染页面
        axios.get('http://elm.cangdu.org/v1/users/3080/addresses').then((res)=>{
            // console.log(res)
            this.list=res.data
        })
    },
    methods:{
        logoods(){
            this.$router.push('/xin')
        },
        //删除
        btn(id){
            axios.delete('http://elm.cangdu.org/v1/users/3080/addresses/'+id).then((res)=>{
                axios.get('http://elm.cangdu.org/v1/users/3080/addresses').then((res)=>{
                    // console.log(res)
                    this.list=res.data
                })
            })
        }
    }

}
</script>

<style lang='scss' scoped>
.header{
  box-sizing: border-box;
  width: 100%;
  height: .91rem;
  background: #3190e8;
  color: white;
  display: flex;
  position: fixed;
  top: 0;
  z-index: 100;
  .span11{
    width: 25%;
    height:.91rem;
    font-size: .4rem;
    text-align: left;
    line-height: .91rem;
  }
  .span2{
    width: 48%;
    height: .91rem;
    font-size: .4rem;
    color: white;
    line-height: .91rem;
    text-align: center;
  }
  .span3{
    width: 25%;
    height: .91rem;
    font-size: .4rem;
    text-align: right;
    line-height: .91rem;
    color: white;
  }
}
.content{
    width: 100%;
    margin-top: .91rem;
    .over{
        height:1.2rem;
        font-size: .32rem;
        background: white;
        border-bottom: 0.01rem solid grey;
        padding-left: 0.2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        ul{
            width: 95%;
            li{
                &:nth-of-type(1){
                    padding: .1rem 0 ;
                }
            }
        }
    }
    .zeng{
        width:100%;
        height: 1rem;
        background: white;
        margin-top: .2rem;
        font-size: .42rem;
        line-height:1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
       span{
           &:nth-of-type(1){
               padding-left: .2rem;
           }
           &:nth-of-type(2){
               padding-right: .2rem;
           }
       }
        }


}
</style>


